@keyframes neon-sweep {
    0%, 100% {
        background-position: -100% 0;
    }
    50% {
        background-position: 200% 0;
    }
}

/* === LAYOUT PRINCIPAL DA TELA === */
.screen-container {
    min-height: 100vh;
    background-color: var(--color-cinza);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Estado inicial: centralizado */
    transition: justify-content 0.3s ease;
}

.screen-container.layout-chat {
    justify-content: flex-start; /* Estado de chat: conteúdo no topo */
}

/* === CABEÇALHO: TÍTULO E BOTÃO DBA FIXO === */
.header-content {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%; /* Necessário para centralização correta do conteúdo flex */
    position: relative; /* Para o ::before do título */
}

.screen-container.layout-chat .header-content {
    margin-top: 2rem;
    /* Se quiser que o cabeçalho alinhe com a largura máxima do chat: */
    /* max-width: 700px; */
}

.estevia-title {
    font-family: 'Zefani', sans-serif;
    color: var(--color-bege);
    text-shadow: 0 0 10px rgba(191, 129, 115, 0.3),
                 0 0 20px rgba(191, 129, 115, 0.2);
    letter-spacing: 10px;
    position: relative;
    overflow: hidden;
    /* A transição de margin-top pode não ser mais necessária aqui se o .header-content a controla */
    /* transition: margin-top 0.3s ease; */
}

.estevia-title::before {
    content: 'ESTEVIA';
    position: absolute;
    top: 0; /* Corrigido de '10' para '0' */
    left: 0;
    width: 100%;
    height: 100%;
    color: var(--color-bege);
    background: linear-gradient(
        90deg,
        transparent 0%,
        rgba(255, 255, 255, 0.8) 50%,
        transparent 100%
    );
    background-size: 200% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: neon-sweep 20s ease-in-out infinite;
}

/* === ÁREA DE CHAT === */
.chat-area {
    opacity: 0;
    transition: opacity 0.3s ease, flex 0.3s ease, max-width 0.3s ease, margin 0.3s ease;
    width: 100%;
    max-width: 500px; 
}

.chat-area.visible {
    opacity: 1;
}

.screen-container.layout-chat .chat-area.visible {
    flex: 1; /* Ocupa o espaço disponível verticalmente */
    max-width: 700px; /* Largura no layout de chat */
    margin: 1rem 0;
    overflow-y: auto;
}

/* === CONTAINER DO INPUT DE CHAT === */
.chat-input-container {
    /* Inicialmente, a largura é definida pelo conteúdo interno (input + botão) */
    width: auto;
    transition: max-width 0.3s ease, padding 0.3s ease, margin-bottom 0.3s ease, width 0.3s ease;
}

.screen-container.layout-chat .chat-input-container {
    width: 100%;
    max-width: 700px;
    padding: 1rem;
    margin-bottom: 1rem;
}

.chat-input-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
}

.chat-input {
    padding: 0px 16px 15px 16px; /* padding-bottom para alinhar texto com botão mais alto */
    width: 500px;
    height: 70px;
    border: 2px solid transparent;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
    color: var(--color-branco);
    font-size: 1rem;
    outline: none;
    transition: border-color 0.3s ease, padding 0.3s ease, height 0.3s ease, width 0.3s ease, flex-grow 0.3s ease;
}

.chat-input:focus {
    border-color: var(--color-bege);
}

.screen-container.layout-chat .chat-input {
    flex: 1;
    padding: 15px 16px; /* Padding uniforme no layout de chat */
    height: 50px;
    width: auto; /* Controlado por flex: 1 */
}

/* === BOTÃO DE ENVIAR === */
.enviar-button {
    background-color: var(--color-bege);
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer;
    transition: background-color 0.3s, width 0.3s ease, height 0.3s ease;
}  

.enviar-button:hover {
  background-color: var(--color-bordo);
}

.paper-plane-icon {
    width: 20px;
    height: 20px;
    color: white;
}

.screen-container.layout-chat .enviar-button {
    width: 50px; 
    height: 50px;
}

/* === BOTÃO DBA === */
/* Estilos base para ambos os estados do botão DBA */
.sql-button {
    position: relative;
    padding: 10px 16px;
    border: 2px solid transparent;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
    color: rgba(255, 255, 255, 0.5);
    font-size: 1rem;
    outline: none;
    transition: border-color 0.3s ease, color 0.3s ease, background-color 0.3s ease;
    background-clip: padding-box; /* Para que o fundo não vaze sob a borda transparente */
    cursor: pointer;
}

.sql-button.selected {
    color: var(--color-branco);
    background-color: rgba(255, 255, 255, 0.2);
    border-color: var(--color-bege);
}

/* Wrapper do botão DBA na sua posição inicial (abaixo do input) */
.sql-button-wrapper-initial {
    margin-top: 10px; 
    display: flex;
    justify-content: flex-start;
}

/* Wrapper do botão DBA na sua posição fixa (ao lado do título) */
.sql-button-wrapper-fixed {
    margin-right: 15px; /* Espaçamento entre "DBA" e "ESTEVIA" */
}

/* Estilo específico para o botão DBA quando fixo, selecionado e desabilitado */
.sql-button-wrapper-fixed .sql-button.selected:disabled {
    /* Herda .sql-button.selected, mas ajusta cursor e opacidade */
    cursor: not-allowed;
    opacity: 0.8; /* Um pouco mais opaco que um :disabled padrão, mas indica estado */
}

/* === ÁREA DE CHAT === */
.chat-area {
    opacity: 0;
    transition: opacity 0.3s ease, flex 0.3s ease, max-width 0.3s ease, margin 0.3s ease;
    width: 100%;
    max-width: 800px; /* Largura inicial, alinhada com input */
    
    /* Adicionado para layout das mensagens */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Espaçamento entre as mensagens */
    padding: 10px; /* Espaçamento interno para as mensagens não colarem nas bordas */
}

.chat-area.visible {
    opacity: 1;
}

.screen-container.layout-chat .chat-area.visible {
    flex: 1; /* Ocupa o espaço disponível verticalmente */
    max-width: 1200px; /* Largura no layout de chat */
    margin: 1rem 0;
    overflow-y: auto; /* Já estava aqui, mantém a rolagem quando o conteúdo excede */
    border: 1px solid rgba(255, 255, 255, 0.1); /* Opcional: uma borda sutil */
    border-radius: 8px; /* Opcional: cantos arredondados */
    background-color: rgba(0,0,0, 0); /* Opcional: um fundo sutil */
}

/* === MENSAGENS INDIVIDUAIS DO CHAT === */
.chat-message {
    padding: 10px 15px;
    border-radius: 18px; /* Bordas mais arredondadas para balões de chat */
    max-width: 80%; /* As mensagens não ocupam a largura total */
    word-wrap: break-word; /* Quebra palavras longas */
    line-height: 1.4;
    text-align: left;
}

.chat-message p {
    margin: 0; /* Remove margem padrão do parágrafo */
}

.chat-message.user {
    background-color: var(--color-bege); /* Cor para mensagens do usuário */
    color: white; /* Cor do texto para usuário */
    align-self: flex-end; /* Alinha à direita */
    border-bottom-right-radius: 4px; /* Para dar um estilo de "balão" */
}

.chat-message.bot {
    background-color: rgba(255, 255, 255, 0.15); /* Cor para mensagens do bot */
    color: var(--color-branco); /* Cor do texto para bot */
    align-self: flex-start; /* Alinha à esquerda */
    border-bottom-left-radius: 4px; /* Para dar um estilo de "balão" */
}

.chat-message ul,
.chat-message ol {
    padding-left: 20px; /* Indentação para listas */
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}

.chat-message li {
    margin-bottom: 0.25em; /* Espaçamento entre itens da lista */
}

.chat-message strong {
    font-weight: bold; /* Garante que o negrito seja aplicado consistentemente */
}

.chat-message em {
    font-style: italic; /* Garante que o itálico seja aplicado consistentemente */
}

/* Ajustes para parágrafos dentro das mensagens para melhor espaçamento */
.chat-message p {
    margin-top: 0; /* Evita margem dupla se o primeiro elemento for um <p> */
    margin-bottom: 0.75em; /* Espaçamento entre parágrafos */
}

/* Remove a margem inferior do último parágrafo dentro de uma mensagem */
.chat-message p:last-child {
    margin-bottom: 0;
}

/* Se você tiver links no markdown, pode querer estilizá-los */
.chat-message a {
    color: var(--color-bege); /* Ou outra cor de link que combine */
    text-decoration: underline;
}

.chat-message a:hover {
    text-decoration: none;
}

/* === ESTILOS PARA O BLOCO DE CÓDIGO SQL (SYNTAXHIGHLIGHTER) === */
/* 
   Estes estilos aplicam-se ao contêiner do SyntaxHighlighter quando 
   ele renderiza um bloco de código SQL.
   Se você usou `className="sql-code-block"` no componente SyntaxHighlighter, 
   você pode usar essa classe. Se você usou `customStyle` para tudo,
   alguns destes podem não ser necessários, mas ter uma classe é bom para especificidade.
*/
.chat-message .sql-code-block { /* Ou apenas .sql-code-block se for global */
    border-radius: 8px;       /* Cantos arredondados para o bloco de código */
    font-size: 0.9em;         /* Ajuste o tamanho da fonte do código se necessário */
    line-height: 1.5;         /* Ajuste a altura da linha para o código */
    overflow-x: auto;         /* Adiciona rolagem horizontal se o código for muito largo */
                                /* e wrapLines={true} não for suficiente ou desejado. */
                                /* Se wrapLines={true} estiver funcionando bem, isso pode não ser necessário. */
    /* O customStyle que você adicionou no componente já deve cuidar de margin, padding, e bg color. */
    /* Exemplo de como seria se não usasse customStyle para padding: */
    /* padding: 1em; */ 
    /* margin: 0; */ /* Para garantir que não haja margens extras no container do código */
}

/* 
   O estilo `okaidia` (ou outro tema do Prism) já vai cuidar da coloração 
   da sintaxe dentro do bloco .sql-code-block.
   Você geralmente não precisa estilizar os tokens individuais (como `span.token.keyword`)
   a menos que queira sobrescrever o tema.
*/

