@font-face {
  font-family: 'Zefani';
  src: url('./assets/fonts/Zefani_Uppercase_Demo-Regular.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

:root {
  --color-branco: #F2F2F0;
  --color-laranja: #A6431F;
  --color-bordo: #731B07;
  --color-bege: #BF8173;
  --color-cinza: #403131;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
